<template>
  <div class="section">
    <div id="char"></div>
  </div>
</template>

<script>
import echarts from 'echarts'
import gl from 'echarts-gl'
export default {
  data () {
    return {
    }
  },

  components: {},

  computed: {},

  mounted: function() {
    this.draw()
  },

  methods: {
    draw() {
      let char = echarts.init(document.getElementById('char'));
      var data = [];
      // Parametric curve
      for (var t = 0; t < 15; t +=3) {
          var x = (1 + 0.25 * Math.cos(75 * t)) * Math.cos(t);
          var y = (1 + 0.25 * Math.cos(75 * t)) * Math.sin(t);
          var z = t + 5 * Math.sin(75 * t);
          data.push([x, y, z]);
      }
      let arr = [];
      for (var t = 0; t < 15; t +=3) {
          var x = (1 + 0.25 * Math.cos(75 * t));
          var y = (1 + 0.25 * Math.cos(75 * t));
          var z = t + 5 * Math.sin(75 * t);
          arr.push([x, y, z]);
      }
      let option = {
        tooltip: {},
        // backgroundColor: '#f00',
        visualMap: {
            show: false,
            dimension: 2,
            min: 0,
            max: 30,
            inRange: {
                // color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
            }
        },
        xAxis3D: {
            name: '南北',
            type: 'value'
        },
        yAxis3D: {
            name: '东西',
            type: 'value'
        },
        zAxis3D: {
            name: '井深',
            type: 'value',
            inverse: false
        },
        grid3D: {
            viewControl: {
                projection: 'orthographic'
            }
        },
        series: [
            {
                type: 'line3D',
                name: '3d',
                data: data,
                lineStyle: {
                    width: 4
                }
            },
            {
                type: 'line3D',
                name: '3d2',
                data: arr,
                lineStyle: {
                    width: 4
                }
            }
        ]
        
      };
      char.setOption(option)
    }
  }
}
</script>

<style lang='scss' scoped>
#char {
  height: 100%;
  background: #fff;
}
</style>



